<template>
<div class="main-page">
  <div class="headerTag">车辆检查</div>
  <form class="h-form">
    <item must name="标题">
      <input v-model="info.FName" />
    </item>
    <item must name="检查人">
      <input v-model="info.PersonName" readonly @click="choosePerson" />
    </item>
    <item must name="检查日期">
      <input v-model="info.FDate" readonly @click="chooseTime('FDate')" />
    </item>
    <item name="检查情况汇总">
<!--      <textarea v-model="info.Content"  /> -->
	  <input v-model="info.Content" />
      </item>
            <div class="headerTag">车辆信息</div>
            <div class="car-item shadow" v-for="(item,index) in info.EntryList" :key="index">
                <div class="car-item-line" :style="{color:$config.theme,fontWeight:800}">车牌号：{{item.FCarNo}}</div>
                <div style="display:flex;flex-wrap: wrap;">
                    <div class="car-item-line">车辆外观：<van-tag :type="item.FAppearance?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FAppearance=0" v-if="item.FAppearance==1" />
                            <van-icon name="cross" size="medium" @click="item.FAppearance=1" v-if="item.FAppearance==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">驾驶舱：<van-tag :type="item.FCockpit?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FCockpit=0" v-if="item.FCockpit==1" />
                            <van-icon name="cross" size="medium" @click="item.FCockpit=1" v-if="item.FCockpit==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">引擎盖内：<van-tag :type="item.FEngineHood?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FEngineHood=0" v-if="item.FEngineHood==1" />
                            <van-icon name="cross" size="medium" @click="item.FEngineHood=1" v-if="item.FEngineHood==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">三检表：<van-tag :type="item.FMeter?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FMeter=0" v-if="item.FMeter==1" />
                            <van-icon name="cross" size="medium" @click="item.FMeter=1" v-if="item.FMeter==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">灭火器检查表：<van-tag :type="item.FExtinguisher?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FExtinguisher=0" v-if="item.FExtinguisher==1" />
                            <van-icon name="cross" size="medium" @click="item.FExtinguisher=1" v-if="item.FExtinguisher==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">车辆系统：<van-tag :type="item.FVehicleSystem?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FVehicleSystem=0" v-if="item.FVehicleSystem==1" />
                            <van-icon name="cross" size="medium" @click="item.FVehicleSystem=1" v-if="item.FVehicleSystem==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">车辆轮胎：<van-tag :type="item.FTyre?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FTyre=0" v-if="item.FTyre==1" />
                            <van-icon name="cross" size="medium" @click="item.FTyre=1" v-if="item.FTyre==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">车辆牌照及附件：<van-tag :type="item.FLicenseTag?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FLicenseTag=0" v-if="item.FLicenseTag==1" />
                            <van-icon name="cross" size="medium" @click="item.FLicenseTag=1" v-if="item.FLicenseTag==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">车辆运行：<van-tag :type="item.FRunningStatus?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FRunningStatus=0" v-if="item.FRunningStatus==1" />
                            <van-icon name="cross" size="medium" @click="item.FRunningStatus=1" v-if="item.FRunningStatus==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">驾驶舱摄像头1：<van-tag :type="item.FCamera1?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FCamera1=0" v-if="item.FCamera1==1" />
                            <van-icon name="cross" size="medium" @click="item.FCamera1=1" v-if="item.FCamera1==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">驾驶舱摄像头2：<van-tag :type="item.FCamera2?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FCamera2=0" v-if="item.FCamera2==1" />
                            <van-icon name="cross" size="medium" @click="item.FCamera2=1" v-if="item.FCamera2==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">车厢内摄像头：<van-tag :type="item.FCamera3?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FCamera3=0" v-if="item.FCamera3==1" />
                            <van-icon name="cross" size="medium" @click="item.FCamera3=1" v-if="item.FCamera3==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">车载监控<van-tag :type="item.FCamera4?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FCamera4=0" v-if="item.FCamera4==1" />
                            <van-icon name="cross" size="medium" @click="item.FCamera4=1" v-if="item.FCamera4==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">图片上传<van-tag :type="item.FCamera5?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FCamera5=0" v-if="item.FCamera5==1" />
                            <van-icon name="cross" size="medium" @click="item.FCamera5=1" v-if="item.FCamera5==0" />
                        </van-tag>
                    </div>
                    <div class="car-item-line">其他情况：<van-tag :type="item.FOthers?'primary':'warning'">
                            <van-icon name="success" size="medium" @click="item.FOthers=0" v-if="item.FOthers==1" />
                            <van-icon name="cross" size="medium" @click="item.FOthers=1" v-if="item.FOthers==0" />
                        </van-tag>
                    </div>
                </div>
            </div>

        </form>
        <van-button type="primary" block size="normal" :color="$config.theme" style="width: calc(100% - 30px); margin:3px 15px" @click="submit" :loading="loading">提交</van-button>
        <van-popup v-model="timeShow" position="bottom">
            <van-datetime-picker type="date" title="选择日期" @confirm="confirmTime" />
        </van-popup>

    </div>
</template>

<script>
export default {
  name: "CarsCheckAE",
  props: {},
  components: {},
  data() {
    return {
      apis: {
        edit: this._api.VMS_Car_CheckEdit,
        create: this._api.VMS_Car_CheckCreate,
        list: this._api.VMS_Car_CheckManage,
        delete: this._api.VMS_Car_CheckDelete,
        audit: this._api.VMS_Car_CheckAudit,
        details: this._api.VMS_Car_CheckDetails
      },
      loading: false,
      curId: null,
      info: {
        PersonName: "",
        FName: "",
        EntryList: []
      },
      rules: {
        FName: "请输入标题！",
        PersonId: "请选择经办人！",
        FDate: "请选择检查日期！"
      },
      curTimeFieldName: "",
      timeShow: false
    }
  },
  methods: {
    async submit() {
      await this.$u.checkForm(this.info, this.rules)
      this.loading = true
      let apiFun = this.curId ? this.apis.edit : this.apis.create
      apiFun(this.info).then(res => {
        if (res) {
          this.$u.msg(this.info.Id ? "编辑成功！" : "创建成功！")
          setTimeout(() => {
            this.$router.back()
          }, 200);
        }
        this.loading = false
      })
    },
    chooseTime(fieldName) {
      this.curTimeFieldName = fieldName
      this.timeShow = true
    },
    confirmTime(time) {
      this.timeShow = false
      this.info[this.curTimeFieldName] = this.$time(time).format("YYYY-MM-DD")
    },
    choosePerson() {
      window.temFun = (person) => {
        this.info.PersonName = person.Fname
        this.info.PersonId = person.Id
      }
      this.$router.push(`/modular/oa/ChoosePerson`)
    },
    VMS_CarIndex() {
      this._api.VMS_CarIndex().then(res => {
        if (res) {
          console.log(res.data)
          this.info.EntryList = res.data.map(it => {
            return {
              FCarNo: it.FCarNo,
              CarId: it.Id,
              FAppearance: 1,
              FCockpit: 1,
              FEngineHood: 1,
              FMeter: 1,
              FExtinguisher: 1,
              FVehicleSystem: 1,
              FTyre: 1,
              FLicenseTag: 1,
              FRunningStatus: 1,
              FCamera1: 1,
              FCamera2: 1,
              FCamera3: 1,
              FCamera4:1,
              FCamera5:1,
              FOthers: 1
            }
          })
        }
      })
    },
  },
  mounted() {
    this.info = {
      EntryList: []
    }
    this.curId = this.$route.query.Id
    this.VMS_CarIndex()
  }
}
</script>

<style lang="scss">
.car-item {
  width: calc(100% - 30px);
  margin-left: 15px;
  margin-top: 15px;
  padding: 0 15px;
  font-size: 14px;

  .car-item-line {
    line-height: 30px;
    margin-right: 15px;
  }
}

</style>
